<template>
    <div>
        <el-row>
            <el-col :span="24">
                <img src="../assets/scolia.png"/>
            </el-col>
        </el-row>

        <el-row class="intro">
            <el-col :span="24">
                <h3>个人简介</h3>
                <span>一路不止</span>
            </el-col>
        </el-row>
        <el-row class="skill">
            <el-col :span="24">
                <h3>个人技能</h3>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="24/skills.length" v-for="skill in skills" :key="skill.name">
                <el-card>
                    <div slot="header" class="skillTitle">
                        <div class="skillName">{{skill.name}}</div>
                        <el-progress type="circle"
                                     :percentage="skill.progress"
                                     class="skillProgress">
                        </el-progress>
                        <i class="skillArrow"
                           :class="{'el-icon-arrow-up': skill.isShow, 'el-icon-arrow-down':!skill.isShow}"
                           @click="skill.isShow=!skill.isShow"></i>
                    </div>
                    <el-collapse-transition>
                        <div class="skillBody" v-show="skill.isShow">
                            <ul>
                                <li v-for="item in skill.details">{{item}}</li>
                            </ul>
                        </div>
                    </el-collapse-transition>
                </el-card>
            </el-col>

        </el-row>
    </div>
</template>

<script>
    class Skill {
        constructor(name, progress, details, isShow=false) {
            this.name = name;
            this.progress = progress;
            this.details = details;
            this.isShow = isShow;
        }
    }

    export default {
        data() {
            return {
                skills: [
                    new Skill("通用技能", 80, [
                        "熟悉基本的数据结构和算法",
                        "了解多线程环境下的开发",
                        "熟悉Linux环境, 掌握常用的shell命令",
                        "熟悉MySQL, 了解SQL优化的要点",
                        "了解NoSQL, 了解Redis的使用",
                        "熟悉git, 了解git工作流",
                        "了解Nginx, supervisor等部署工具的使用"
                    ]),
                    new Skill("Python", 70, [
                        "熟悉Django, Flask的web框架",
                        "能熟练使用python进行日志分析"
                    ]),
                    new Skill("Java", 75, [
                        "熟悉spring, springMVC, Mybatis, Shiro等框架",
                        "掌握使用Maven进行依赖管理",
                        "掌握使用Junit进行单元测试",
                        "了解Tomcat的使用"
                    ]),
                    new Skill("JavaScript", 80, [
                        "掌握webpack下的vue开发",
                        "了解vue-router, axios, element-ui的使用",
                        "熟悉Jquery",
                        "熟悉bootstrap",
                        "熟悉Json",
                        "熟悉Ajax",
                        "了解es6"
                        ])
                ],
            }
        },

    }
</script>

<style scoped>
    .intro {
        text-align: center;
        margin-top: 30px;
        margin-bottom: 20px;
    }
    .intro span {
        font-style: italic;
    }

    .skill {
        text-align: center;
        margin-top: 10px;
    }

    .skillTitle, .skillBody {
        text-align: center;
    }
    .skillName {
        margin-bottom: 10px;
    }
    .skillBody {
        text-align: left;
        line-height: 1.7;
    }
    .skillArrow {
        margin-top: 5px;
        display: block;
    }
</style>



